<template>
  <div>
    <h1>拖拽指令</h1>
<p><code>拖动一张图片的时候，v-drag赋值，v-drop取值</code></p>
<blockquote>
<p>李嘉宾</p>
</blockquote>
<h2>Example:</h2><div class="demo-template"><template>
  <div class="flex">
    <div class="drag">
      <ul>
        <li v-for="(person, index) in ALL" :key="person + index"><span v-drag="person">{{ person }}</span></li>
      </ul>
    </div>
    <div v-drop="drop" class="drop">
      {{data ? data : 'drop here'}}
    </div>
  </div>
</template></div><h2>Code:</h2><pre v-pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"flex"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"drag"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">ul</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">li</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(person, index) in ALL"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"person + index"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">v-drag</span>=<span class="hljs-string">"person"</span>&gt;</span>{{ person }}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">ul</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">v-drop</span>=<span class="hljs-string">"drop"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"drop"</span>&gt;</span>
      {{data ? data : 'drop here'}}
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">const</span> ALL = <span class="hljs-string">`1、用微笑告诉别人，今天的你比昨天更坚强。
2、没有比脚更远的路，没有比人更高的山！
3、不是要做一个单纯优秀的人，而是要做一个不可替代的人。
4、原本认为自己做不到的事最后却做成了，感觉真的很好！
5、所谓成熟，就是越来越发觉以前的自己是个傻逼。
6、人生就象一个球，无论如何滚来滚去，总有在一个点上停止的时候。
7、勤奋是你的密码，能译出你一部壮丽的史诗。
8、观察走在你前面的人，看看他为何领先，学习他的优点。
9、你曾经不被人所爱，你才会珍惜将来那个爱你的人。
10、不是谁什么都有，也不是谁什么都没有。`</span>.split(<span class="hljs-regexp">/\s+/</span>);

  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        ALL,
        <span class="hljs-attr">data</span>: <span class="hljs-literal">null</span>,
      };
    },
    <span class="hljs-attr">methods</span>: {
      drop(data) {
        <span class="hljs-keyword">this</span>.data = data;
      },
    },
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-comment">&lt;!-- &lt;style lang="scss" scoped&gt;
  .flex {
    display: flex;
  }
  .drag {
    text-align: center;
    span {
      display: inline-block;
      cursor: move;
      padding: 5px;
      border-radius: 8px;
      border: 1px solid #abc;
    }
    li {
      margin: 10px;
      line-height: 20px;
    }
  }
  .drop {
    border: 1px solid #ccc;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    transition: 1s ease all;
    border-radius: 8px;
    .isDrag {
      box-shadow: 0px 0px 1000px 1px rgba(66, 133, 244, 1) inset;
    }
  }
&lt;/style&gt; --&gt;</span>
</code></pre>

  </div>
</template>



<script>
  const ALL = `1、用微笑告诉别人，今天的你比昨天更坚强。
2、没有比脚更远的路，没有比人更高的山！
3、不是要做一个单纯优秀的人，而是要做一个不可替代的人。
4、原本认为自己做不到的事最后却做成了，感觉真的很好！
5、所谓成熟，就是越来越发觉以前的自己是个傻逼。
6、人生就象一个球，无论如何滚来滚去，总有在一个点上停止的时候。
7、勤奋是你的密码，能译出你一部壮丽的史诗。
8、观察走在你前面的人，看看他为何领先，学习他的优点。
9、你曾经不被人所爱，你才会珍惜将来那个爱你的人。
10、不是谁什么都有，也不是谁什么都没有。`.split(/\s+/);

  export default {
    data() {
      return {
        ALL,
        data: null,
      };
    },
    methods: {
      drop(data) {
        this.data = data;
      },
    },
  };
</script>

<!-- <style lang="scss" scoped>
  .flex {
    display: flex;
  }
  .drag {
    text-align: center;
    span {
      display: inline-block;
      cursor: move;
      padding: 5px;
      border-radius: 8px;
      border: 1px solid #abc;
    }
    li {
      margin: 10px;
      line-height: 20px;
    }
  }
  .drop {
    border: 1px solid #ccc;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    transition: 1s ease all;
    border-radius: 8px;
    .isDrag {
      box-shadow: 0px 0px 1000px 1px rgba(66, 133, 244, 1) inset;
    }
  }
</style> -->



