<template>
  <div>
    <h1>右键菜单</h1>
<p><code>在rz-image-card上点击右键出现右键菜单 v-menu=&quot;{ imageData: { src: 'xxx', smallSrc: 'xxx' } }&quot;</code></p>
<blockquote>
<p>李鹏志 &amp; 林桢</p>
</blockquote>
<h2>Example:</h2><div class="demo-template"><template>
  <div class="flex">
    <div>
      <span>情况A: 在检索模块</span>
      <rz-image-card v-menu="{ imageData: { src: data2.src, smallSrc: data2.src }, type: 'INTELLIGENT' }" :data="data2" :transition="false"></rz-image-card>
    </div>
    <div>
      <span>情况B: 不在检索模块</span>
      <rz-image-card v-menu="{ imageData: getImageSrc(data2) }" :data="data2" :showInfo="false"></rz-image-card>
    </div>
    <div>
      <span>情况C: 有回调事件的</span>
      <rz-image-card v-menu="{ imageData: getImageSrc(data2), menuClicked, }"" :data="data2" :showInfo="false"></rz-image-card>
    </div>
    <div>
      <span>情况D: 不传会没有效果, 并会抛出异常</span>
      <rz-image-card v-menu="{ imageData: {} }" :data="data2" :showInfo="false"></rz-image-card>
    </div>
  </div>
</template></div><h2>Code:</h2><pre v-pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"flex"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>情况A: 在检索模块<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">rz-image-card</span> <span class="hljs-attr">v-menu</span>=<span class="hljs-string">"{ imageData: { src: data2.src, smallSrc: data2.src }, type: 'INTELLIGENT' }"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data2"</span> <span class="hljs-attr">:transition</span>=<span class="hljs-string">"false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">rz-image-card</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>情况B: 不在检索模块<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">rz-image-card</span> <span class="hljs-attr">v-menu</span>=<span class="hljs-string">"{ imageData: getImageSrc(data2) }"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data2"</span> <span class="hljs-attr">:showInfo</span>=<span class="hljs-string">"false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">rz-image-card</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>情况C: 有回调事件的<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">rz-image-card</span> <span class="hljs-attr">v-menu</span>=<span class="hljs-string">"{ imageData: getImageSrc(data2), menuClicked, }"</span>" <span class="hljs-attr">:data</span>=<span class="hljs-string">"data2"</span> <span class="hljs-attr">:showInfo</span>=<span class="hljs-string">"false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">rz-image-card</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>情况D: 不传会没有效果, 并会抛出异常<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">rz-image-card</span> <span class="hljs-attr">v-menu</span>=<span class="hljs-string">"{ imageData: {} }"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data2"</span> <span class="hljs-attr">:showInfo</span>=<span class="hljs-string">"false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">rz-image-card</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">data2</span>: {
          <span class="hljs-attr">src</span>: <span class="hljs-string">"http://n.sinaimg.cn/translate/600/w1920h1080/20180424/6Gzs-fzqvvsa3882201.jpg"</span>,
          <span class="hljs-attr">width</span>: <span class="hljs-number">128</span>,
          <span class="hljs-attr">height</span>: <span class="hljs-number">160</span>,
          <span class="hljs-attr">bounding</span>: { <span class="hljs-attr">start</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">508</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">302</span> }, <span class="hljs-attr">end</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">608</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">406</span> } },
          <span class="hljs-attr">info</span>: <span class="hljs-string">"90%"</span>,
        },
      };
    },
    <span class="hljs-attr">methods</span>: {
      getImageSrc(item) {
        <span class="hljs-keyword">return</span> {
          <span class="hljs-attr">src</span>: item.src,
          <span class="hljs-attr">smallSrc</span>: item.src,
        };
      },
      <span class="hljs-comment">// 右键菜单点击的回调事件</span>
      menuClicked() {
        alert(<span class="hljs-string">'contextmenu has clicked'</span>);
      },
    },
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-comment">&lt;!-- &lt;style lang="scss" scoped&gt;
  .flex {
    display: flex;
    justify-content: space-around;
  }
&lt;/style&gt; --&gt;</span>
</code></pre>
<h2>Attributes</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>imageData（必选）</td>
<td>{ src: '大图src'（没大图则传入小图 src）, smallSrc: '小图src' } 两个字段必传</td>
<td>object</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type（可选）</td>
<td>模块类型: 情况 A：需传入对应模块简称（例：人脸检索 - FACE)； 情况 B：DEFAULT</td>
<td>string</td>
<td>—</td>
<td>DEFAULT</td>
</tr>
<tr>
<td>menuClicked（可选）</td>
<td>针对一些组件，比如弹窗之类的点击右键后的一些操作</td>
<td>function</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>

  </div>
</template>



<script>
  export default {
    data() {
      return {
        data2: {
          src: "http://n.sinaimg.cn/translate/600/w1920h1080/20180424/6Gzs-fzqvvsa3882201.jpg",
          width: 128,
          height: 160,
          bounding: { start: { x: 508, y: 302 }, end: { x: 608, y: 406 } },
          info: "90%",
        },
      };
    },
    methods: {
      getImageSrc(item) {
        return {
          src: item.src,
          smallSrc: item.src,
        };
      },
      // 右键菜单点击的回调事件
      menuClicked() {
        alert('contextmenu has clicked');
      },
    },
  };
</script>

